import Vue from 'vue';
import Vuex from 'vuex';
import { parseActionData, parseContractData, isContractForBalance } from './gekoUtils';
import { get } from '@nn/psdl-utility/src/object-utils'
import { api, config } from '@nn/nn-api';
import { warn, error } from '@nn/psdl-utility/src/console';

config.setFetchMethod(fetch);
config.setFetchCredentials('same-origin');

Vue.use(Vuex);

const mutations = {
  updateStore(state, { contracts, actions, account, language, contractsState }) {
    state.contracts = contracts || state.contracts;
    state.actions = actions || state.actions;
    state.account = account || state.account;
    state.language = language || state.language;
    state.contractsState = contractsState || state.contractsState;
  },
};

const actions = {
  getContracts({ commit }) {

    commit('updateStore', { contractsState: 'loading' });

    return api.customer
      .getCustomerDataPortal('GEKO')
      .then((data) => {
        const account = get(data, 'Body.AccountData');
        const actions = parseActionData(get(data, 'Body.ActionList.Action'));
        const businessPartner = get(data, 'Body.BusinessPartnerData');
        const contracts = parseContractData(get(data, 'Body.ContractList.ContractData'));

        commit('updateStore', {
          actions: actions,
          account: account,
          language: businessPartner.CorrespondentieTaal,
          contracts: contracts,
          contractsState: 'loaded'
        });
      })
      .catch((err) => {
        commit('updateStore', { contractsState: 'error' });
        error(err);
      });
  },

  getBalances({ commit, state }) {
    const allContractsForBalances = state.contracts.reduce((result, cat) => {
      return result.concat(cat.contracts.filter(isContractForBalance));
    }, []);

    return Promise.all(allContractsForBalances
      .map((contract) => {
        let promise;
        if (contract.VTA_id === 'OHPN') {
          promise = api.bank.getBalanceOFS(contract.VTA_Contract_id, contract.VTA_id, contract.SAPBP_SR_ID);
        }
        if (contract.VTA_id === 'BANK') {
          promise = api.bank.getBalanceEP(contract.VTA_Contract_id, contract.VTA_id)
        }

        if(!promise) {
          return;
        }

        return promise.then((data) => {
          contract.Saldo = get(data, 'AccountBalance.TotalAccountBalance');
          contract.saldoLoadingState = 'loaded';
        })
          .catch((error) => {
            contract.saldoLoadingState = 'error';
            warn(error);
          })
      }))
      .then(() => {
        commit('updateStore', { contracts: state.contracts });
      })
      .catch((err) => {
        error(err);
      });
  },
};

const getters = {
  contractDefaultAction: (state) => (id) => {
    let defaultAction = state.actions.find(a => a.ProductId === id && a.Action === 'Details bekijken');
    if (!defaultAction) {
      defaultAction = state.actions.filter(a => a.ProductId === id && a.Action !== 'Details bekijken')[0]
    }
    return defaultAction;
  },
  contractExtraActions: (state) => (id) => {
    const defaultAction = getters.contractDefaultAction(state)(id);
    return state.actions.filter(a => a.ProductId === id && a.Action !== 'Details bekijken' && a.Action !== defaultAction.Action);
  }
};

const store = new Vuex.Store({
  state: {
    contractsState: 'init',
    account: {},
    actions: [],
    language: '',
    contracts: [],
  },
  mutations,
  actions,
  getters
});

export { store, actions, mutations, getters }
