import template from './contractItem.vue.html';
import anchor from './anchor';
import {mapGetters, mapActions } from 'vuex';
import { putGetAndRedirect } from '../utility/redirect';
import { createContractProfile } from '../utility/profilesFactory';

export default {
  props: {
    contract: Object,
    account: Object,
    language: String,
  },
  template,
  components: {
    Anchor: anchor
  },
  computed: {
    ...mapGetters([
      'contractDefaultAction',
      'contractExtraActions'
    ]),
    defaultAction() {
      return this.contractDefaultAction(this.contract.ProductIdForActionList);
    },
    extraActions() {
      return this.contractExtraActions(this.contract.ProductIdForActionList);
    },
    inactive() {
      if (!this.contractDefaultAction(this.contract.ProductIdForActionList)) {
        return true;
      }
      return false;
    },
    showSaldo: function () {
      return !!this.contract.saldoLoadingState;
    },
    contentClass() {
      return { 'u-color--medium-grey': this.inactive }
    },
  },
  data() {
    return { loading: false };
  },
  methods: {
    ...mapActions([
      'getRedirectUrl'
    ]),
    async goToAction(action) {
      this.loading = true;

      const contractProfile = createContractProfile({
        enddate: this.contract.EndDate,
        externalId: this.contract.ExternalId,
        externalIdAdm: this.contract.ExternalIdAdm,
        contractNumber: this.contract.VTA_Contract_id,
        language: this.language,
        mrdbid: (this.account.Party) ? this.account.Party.PartyId : null,
        productCode: this.contract.PRODUCTCODE,
        roleid: this.contract.Role,
        rolenumber: this.contract.RoleSequenceNumber,
        sapbp_srid: this.contract.SAPBP_SR_ID,
        sapbpId: (this.account.Party) ? this.account.Party.PartyId : null,
        vaNumber: this.contract.VAnummer,
        vtaId: this.contract.VTA_id,
      }, 'GEKO');

      await putGetAndRedirect({
        profile: contractProfile,
        action,
      });
      this.loading = false;
    }
  }
}