<template>
  <div class="u-padding-top--10" v-html="content"></div>
</template>

<script>
import { getContentFromUrl } from "./loadCmsContent";

export default {
  name: "CmsContent",
  props: ['contentUrl'],
  data: () => ({
    content: ''
  }),
  async mounted() {
    this.content = await getContentFromUrl(this.contentUrl);
  }
}
</script>

