const publishMfaTracking = (eventAction, eventLabel) => {
  window.app.analytics.publish('mfa-tracking', 'login-qr', {
    'event_action': eventAction,
    'event_label': eventLabel
  });
}

const publishLoginFormTracking = (eventAction, eventLabel) => {
  window.app.analytics.publish('login-form-tracking', 'login-form', {
    'event_action': eventAction,
    'event_label': eventLabel
  });
}

export default { publishMfaTracking, publishLoginFormTracking }