<template>
  <div class="qr-container" :style="canvasSize">
    <canvas ref="canvas"></canvas>
    <div v-if="expired" class="qr-overlay" :style="canvasSize" @click="reload()">
      <div class="qr-overlay-content">
        <nn-spinner v-if="loading"></nn-spinner>
        <nn-button v-else><img :src="require('./img/refresh.svg')" :alt="$t('APPLOGIN_QR_REFRESH')" height="15">
          {{$t("APPLOGIN_QR_REFRESH")}}
        </nn-button>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode';

export default {
  name: "qr-code",
  props: ['qrCode', 'timeout'],
  data: () => ({
    expired: false,
    loading: false,
    canvasSize: {
      height: 0,
      width: 0,
    },
  }),
  watch: {
    qrCode: {
      handler(newValue, oldValue) {
        if (newValue !== oldValue) {
          this.generate();
        }
      },
    },
  },
  mounted () {
    this.generate();
  },
  methods: {
    reload() {
      this.$emit('refresh');
      this.loading = true;
    },
    generate() {
      this.expired = false;
      this.loading = false;
      if (this.$refs.canvas && this.qrCode) {
        QRCode.toCanvas(this.$refs.canvas, this.qrCode, {
          color: {
            dark: "#414141FF",
            light: "#FFFFFFFF"
          },
          margin: 0,
        }, (error) => {
          if (error) {
            this.$emit('error', 'qr-code-invalid-data')
          } else {
            this.drawLogo();
            this.canvasSize = {
              width: this.$refs.canvas.width + 'px',
              height: this.$refs.canvas.height + 'px',
            };

            if (this.timeout) {
              setTimeout(() => {
                this.expired = true;
              }, this.timeout)
            }
          }
        })
      } else {
        this.$emit('error', 'qr-code-missing-data')
      }
    },
    drawLogo() {
      const context = this.$refs.canvas.getContext('2d');
      const logoDims = {
        width: 16,
        height: 16
      };

      const dims = {
        canvasCentreX: this.$refs.canvas.width / 2,
        canvasCentreY: this.$refs.canvas.height / 2,
        backgroundRadius: logoDims.width / 2 + 2,
        startAngle: 0,
        endAngle: 2 * Math.PI,
        logoX: this.$refs.canvas.width / 2 - logoDims.width / 2,
        logoY: this.$refs.canvas.height / 2 - logoDims.height / 2
      };

      const logo = new Image();
      logo.src = require('./img/NN.svg');
      logo.background = "white";
      logo.onload = function () {
        context.fillStyle = 'white';
        context.beginPath();
        context.arc(dims.canvasCentreX, dims.canvasCentreY, dims.backgroundRadius, dims.startAngle, dims.endAngle);
        context.fill();
        context.drawImage(logo, dims.logoX, dims.logoY, logoDims.width, logoDims.height);
      };
    }
  }
}
</script>

<style scoped>
  .qr-container {
    position: relative;
  }

  canvas, .qr-overlay {
    position: absolute;
  }

  .qr-overlay {
    background-color: rgba(255, 255, 255, .85);
    display: flex;
    align-items: center;
  }

  .qr-overlay .qr-overlay-content {
    margin-left: auto;
    margin-right: auto;
  }
</style>
