import Vue from 'vue';
import Vuex from 'vuex';
import { error } from '@nn/psdl-utility/src/console';
import { buildQueryString } from '@nn/psdl-utility/src/query-string';

Vue.use(Vuex);

const parseNotifications = (notifications) => {
  if (!notifications) {
    return [];
  }
  return notifications.map(n => {
    if (n.Contract.Contract_ID) {
      n.Contract.VTA_Contract_id = n.Contract.Contract_ID;
      delete n.Contract.Contract_ID;
    }
    if (n.Contract.VTA_ID) {
      n.Contract.VTA_id = n.Contract.VTA_ID;
      delete n.Contract.VTA_ID;
    }
    return n;
  });
};

const mutations = {
  setNotifications(state, notifications) {
    state.notifications = notifications;
  },
  setStatus(state, status) {
    state.status = status;
  },
  setConfig(state, config) {
    state.config.feed = config.feed;
    state.config.actionFeed = config.actionFeed;
    state.config.origin = config.origin;
  },
};

const actions = {
  setConfig({ commit }, config) {
    commit('setConfig', config);
  },
  async getNotifications({ commit, state }) {
    try {
      commit('setStatus', 'pending');
      const response = await fetch(state.config.feed, {
        method: 'POST',
        credentials: 'same-origin',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: buildQueryString({ "kanaal": "nn" }),
      });
      if (response.ok) {
        const json = await response.json();
        commit('setStatus', 'ready');
        commit('setNotifications', parseNotifications(json.Notifications));
      } else {
        commit('setStatus', 'error');
      }
    } catch (err) {
      error(err);
    }
  },
  async removeNotification({ commit, state }, { notification, context }) {

    try {
      const notifications = state.notifications.filter(n => n !== notification);
      commit('setNotifications', notifications);
      const response = await fetch(state.config.actionFeed, {
        body: buildQueryString(context),
        method: 'POST',
        credentials: 'same-origin',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
      });
      if (!response.ok) {
        commit('setStatus', 'error');
      }
    } catch (err) {
      error(err);
    }
  }
};

const store = new Vuex.Store({
  state: {
    notifications: [],
    status: 'ready',
    config: {},
  },
  actions,
  mutations,
});

export { store, actions, mutations };
