import { assign as redirectTo, pathname } from "@nn/psdl-utility/src/location";
import scrapeContent from "../../utility/content-scraper";
import cookie from "@nn/psdl-utility/src/cookie";
import { error } from "@nn/psdl-utility/src/console";
import { api, config } from '@nn/nn-api';
import errorState from "./errorState";
import { redirect } from "../shared/redirect";
import { getAuthSessionId } from '../../app-utilities/forgeRockUtils';

config.setFetchMethod(fetch);
config.setFetchCredentials('same-origin');

let loginConfig = {};

export function setConfig(config) {
  loginConfig = config
}

export function loginSuccess() {
  redirect(loginConfig.orgUrl, loginConfig.loginRedirect);
}

export function loginBlocked(reason) {
  if (reason === "AccountInactief" && loginConfig.blockedRedirect) {
    return redirectTo(loginConfig.blockedRedirect);
  }

  if (reason === "AccountInactief" && loginConfig.blockedContentUrl) {
    return getErrorFromUrl(loginConfig.blockedContentUrl);
  }

  return errorState.invalid;
}

export function loginInvalid() {
  if (loginConfig.invalidRedirect &&
    pathname().toUpperCase() !== loginConfig.invalidRedirect.split('?', 1).shift().toUpperCase()
  ) {
    return redirectTo(loginConfig.invalidRedirect);
  }
  return errorState.invalid;
}

export function loginConflict(reason) {
  if(reason.toLowerCase() === 'mfa required') {
    return errorState.mfaEnabled;
  }
  return errorState.serviceError;
}

async function tryToGetAuthSessionIdFromForgeRock () {
  try {
    await getAuthSessionId();
  } catch (err) {
    error(err);
  }
}

async function handleLoginResponse({ status, result, redirect, reason }) {
  if (!result) {
    return rejectServiceError();
  }

  if (redirect) {
    await tryToGetAuthSessionIdFromForgeRock();
    return Promise.resolve(redirectTo(redirect));
  }

  if (result.toLowerCase() === 'ok') {
    await tryToGetAuthSessionIdFromForgeRock();
    return Promise.resolve(loginSuccess());
  }

  if (status === "403") {
    return Promise.reject(loginBlocked(reason));
  }

  if (status === "401") {
    return Promise.reject(loginInvalid());
  }

  if (status === "409") {
    return Promise.reject(loginConflict(reason));
  }

  return rejectServiceError();
}

export function login(username, password) {
  return api.generic.login(username, unescape(encodeURIComponent(password)))
    .catch(rejectServiceError) // catch must be before handleLoginResponse to properly propagate further rejects
    .then(handleLoginResponse);
}

export function getErrorFromUrl(url) {
  return scrapeContent(url)
    .catch(rejectInvalidError);
}

function rejectInvalidError() {
  return Promise.reject(errorState.invalid);
}


function rejectServiceError() {
  return Promise.reject(errorState.serviceError);
}

export function rememberUsername(username) {
  cookie.set(loginConfig.usernameCookieName, username, loginConfig.usernameCookieLifetime);
}

export function forgetUsername() {
  cookie.remove(loginConfig.usernameCookieName);
}

export function getSavedUsername() {
  const savedUserName = cookie.get(loginConfig.usernameCookieName);
  if (typeof savedUserName === 'string') {
    return savedUserName;
  }

  return '';
}


export default {
  setConfig,
  rememberUsername,
  forgetUsername,
  getSavedUsername,
  login,
}
