import Template from './notificationItem.vue.html';

const NotificationItem = {
  template: Template,
  props: {
    item: Object,
    origin: String,
    onRemoveNotification: Function,
    onCallToAction: Function,
  },
  data: function () {
    return { show: true, };
  },
  methods: {
    onAfterLeave () {
      const context = {
        "Origin": this.origin,
        "VTA_ID": this.item.Contract.VTA_id,
        "Contract_ID": this.item.Contract.VTA_Contract_id,
        "BerichtCode": this.item.Message.Code,
        "BerichtType": this.item.Message.Type,
        "TrackCode": this.item.Message.TrackCode,
      };
      this.onRemoveNotification(this.item, context);
    },
    onCloseButtonClicked () {
      this.show = false;
    },
    onActionClicked () {
      this.onCallToAction(
        this.item,
        `${this.item.Action.Href}?nn_mb_id=${this.item.Message.TrackCode}`
      );
    },
  },
  computed: {
    itemClasses: function () {
      const { Type } = this.item.Message;
      const classes = {
        'c-notification': true,
        'c-notification--urgent': true,
        'u-margin-bottom': true,
        'c-notification--closable': Type.indexOf('2') >= 0,
      };
      classes[messageTypes[Type.toUpperCase()]] = true;
      return classes;
    },
    actionClasses: function () {
      return {
        'c-link': this.item.Message.Type.indexOf('2') >= 0,
        'c-button': this.item.Message.Type.indexOf('2') === -1
      };
    },
  }
};

const messageTypes = {
  "00": "c-notification--warning c-notification--large-icon",
  "1A": "c-notification--success c-notification--large-icon",
  "1B": "c-notification--warning c-notification--large-icon",
  "1C": "c-notification--information c-notification--large-icon",
  "1D": "c-notification--error c-notification--large-icon",
  "2A": "c-notification--warning",
  "2B": "c-notification--information",
  "2D": "c-notification--error",
};

export default NotificationItem;