export default {
  created () {
    this.analytics = {
      prop () {
        if (window.app && window.app.analytics) {
          window.app.analytics.prop.apply(window.app.analytics, arguments);
        }
      },
      publish () {
        if (window.app && window.app.analytics) {
          window.app.analytics.publish.apply(window.app.analytics, arguments);
        }
      },
    };
  }
};
