import { getRedirectUrl, pfcUrl } from "../app-utilities/utilities";
import * as location from '@nn/psdl-utility/src/location';
import { error } from '@nn/psdl-utility/src/console';
import { api, config } from '@nn/nn-api';

config.setFetchMethod(fetch);
config.setFetchCredentials('same-origin');

export async function putGetAndRedirect({ redirectUrl, profile, action }) {
  if (action && action.ParameterFormat === 'PUTGET') {
    try {
      const url = await buildUrl(action, redirectUrl, profile);
      location.assign(url);
    } catch (err) {
      error(err);
    }
  }

  if (action && (action.ParameterFormat === 'PUTGEBLANK' || action.ParameterFormat === 'PUTGETBLANK')) {
    let newTab;
    if (navigator.userAgent.match(/Trident.*rv:11\./)) {
      newTab = location.open('', '', 'left=0,top=0,menubar=yes,location=yes,resizable=yes,scrollbars=yes,status=yes,width=' + screen.width + ',height=' + screen.height);
    } else {
      newTab = location.open('');
    }
    try {
      newTab.document.body.innerHTML = '<span>De opgevraagde pagina wordt geladen</span>';
      newTab.location.href = await buildUrl(action, redirectUrl, profile);
    } catch (err) {
      error(err);
      newTab.document.body.innerHTML = '<span>Er is een fout opgetreden!</span>';
    }
  }
}

const buildUrl = (action, redirectUrl, profile) => {
  if (!redirectUrl) {
    redirectUrl = pfcUrl(action.PFC_Product, action.PFC_Functie, action.PFC_Context);
  }

  return getTicketId(profile)
    .then(ticketid => {
      return getRedirectUrl({
        'profile': profile.name,
        'redirectUrl': redirectUrl,
        'ticketId': ticketid
      });
    })
    .catch(error);
}

const getTicketId = (profile) => {
  return api.generic
    .putDataForTicketID(profile.origin, "mijn.nn.nl", profile.ticketXml)
    .then((ticketData) => ticketData.Id);
};