<template>
  <div>
    <div class="hide-on-desktop u-margin-bottom--20" >
      <nn-notification v-if="mfaEnabled" type="info" class="hide-on-non-mobile u-margin-bottom--30">{{ $t("APPLOGIN_SUGGESTMFAMOBILE") }}</nn-notification>
      <nn-button variant="secondary" :href="openApp" @click="openingApp = true" id="open-app-button" :tabindex="tabindex">
        {{ config.withNnAppButtonText? config.withNnAppButtonText : $t("APPLOGIN_WITHNNAPP") }}
        <span class="c-icon c-icon--chevron-right u-margin-left--5"></span>
      </nn-button>
    </div>

    <nn-notification v-if="openingApp" type="info" class="u-margin-bottom--30">{{ $t("APPLOGIN_OPENINGAPP") }}</nn-notification>

    <div class="hide-on-mobile" id="non-mobile-element">
      <nn-radio-button-group mode="panel" role="tablist" :label="$t('APPLOGIN_HOWTO')" class="u-margin-bottom--30 login-tabs">
        <nn-radio-button name="loginForm" v-model="selected" value="form" class="u-textalign-center" :tabindex="tabindex">{{ $t("APPLOGIN_WITHLOGINFORM") }}</nn-radio-button>
        <nn-radio-button name="qrCode" v-model="selected" value="qrCode" class="u-textalign-center">
          {{ config.qrCodeButtonText ? config.qrCodeButtonText : $t("APPLOGIN_WITHQRCODE") }}
        </nn-radio-button>
      </nn-radio-button-group>
      <div v-if="selected === 'form'">
        <login-form :config="config" id="non-mobile-login-form" @mfaEnabled="suggestMfa()"></login-form>
      </div>
      <div v-if="selected === 'qrCode'">
        <div v-if="mfaEnabled" class="u-margin-bottom--30">
          <nn-notification type="info" id="sugest-mfa-tablet-notification" class="hide-on-desktop">{{ $t("APPLOGIN_SUGGESTMFATABLET") }}</nn-notification>
          <nn-notification type="info" id="sugest-mfa-desktop-notification" class="hide-on-non-desktop">{{ $t("APPLOGIN_SUGGESTMFADESKTOP") }}</nn-notification>
        </div>
        <login-qr-code :config="config" id="non-mobile-login-qr"></login-qr-code>
      </div>
    </div>

    <div class="hide-on-non-mobile" id="mobile-element">
      <login-form :config="config" id="mobile-login-form" @mfaEnabled="suggestMfa()"></login-form>
    </div>
  </div>
</template>

<script>
import LoginForm from '../loginForm/LoginForm'
import LoginQrCode from '../loginQrCode/LoginQrCode'
import { makeRelative } from "../shared/redirect";
import { getTabIndex } from '../shared/tabindex';
import cookieUtils from '../shared/cookieUtils';

export default {
  name: "LoginMfa",
  data: () => ({
    selected: cookieUtils.getLoginTabCookie() ? cookieUtils.getLoginTabCookie() : 'form',
    openingApp: false,
    mfaEnabled: false,
  }),
  props: {
    config: Object,
  },
  components: { LoginForm, LoginQrCode },
  computed: {
    openApp() {
      const relOrgUrl = makeRelative(this.config.orgUrl);
      const redirectUrl = (relOrgUrl && relOrgUrl !== '/') ? relOrgUrl : this.config.loginRedirect;
      return `${this.config.urlOpenNnApp}${encodeURIComponent(redirectUrl)}`
    },
    tabindex() {
      return getTabIndex();
    },
  },
  methods: {
    suggestMfa() {
      this.mfaEnabled = true;
      this.selected = 'qrCode';
    },
  },
}
</script>

<style lang="scss" scoped>
  @import '../../_helpers/_sizes.scss';

  .hide-on-mobile {
    display: block;
  }
  .hide-on-non-mobile {
    display: none;
  }

  @media (max-width: $tabmob_break_point) {
    .hide-on-mobile {
      display: none;
    }
    .hide-on-non-mobile {
      display: block;
    }
  }

  .hide-on-desktop {
    display: block;
  }

  .hide-on-non-desktop {
    display: none;
  }

  @media (min-width: $desktop_break_point) {
    .hide-on-desktop {
      display: none;
    }

    .hide-on-non-desktop {
      display: block;
    }
  }

  .login-tabs {
    label {
      flex-grow: 1;
      flex-basis: 0;
    }
  }

  .image-tab {
    display: flex;

    img {
      margin-right: 2px;
      height: 20px;
    }
  }
</style>