<template>
  <div>
    <form @submit.prevent="login">
      <div class="u-margin-bottom">
        <nn-input-field
          :id="'username_' + _uid"
          name="username"
          v-model="username"
          :tabindex="tabindex"
          full-width
          ref="username"
        >
          {{ $t("APPLOGIN_USERNAME") }}
          (<a :href="config.linkGebruikersnaamVergeten">{{ $t("APPLOGIN_FORGOTUSERNAME") }}</a>)
        </nn-input-field>
      </div>

      <div class="u-margin-bottom">
        <nn-input-field
          type="password"
          :id="'password_' + _uid"
          v-model="password"
          name="password"
          :tabindex="tabindex"
          full-width
          @keydown="onKeydown($event)"
          :hint="capsLockInfo"
          @blur="onBlur()"
        >
          {{ $t("APPLOGIN_PASSWORD") }}
          (<a :href="config.linkWachtwoordVergeten">{{ $t("APPLOGIN_FORGOTPASSWORD") }}</a>)
        </nn-input-field>
      </div>

      <div class="u-margin-bottom">
        <nn-checkbox
          :id="'remember_' + _uid"
          name="remember"
          v-model="remember"
          :tabindex="tabindex"
        >
          {{ $t("APPLOGIN_REMEMBERME") }}
        </nn-checkbox>
      </div>

      <nn-notification v-if="invalidFormError" type="warning" class="u-margin-bottom"
        v-html="$t('APPLOGIN_INVALIDFORM', config)">
      </nn-notification>
      <nn-notification v-else-if="serviceError" type="error" class="u-margin-bottom">
        {{ $t("APPLOGIN_ERROR") }}
      </nn-notification>
      <nn-notification v-else-if="error" type="error" class="u-margin-bottom">
        {{error}}
      </nn-notification>

      <div class="u-margin-bottom">
        <nn-button type="submit" name="login" :tabindex="tabindex">
          {{ config.loginButtonText ? config.loginButtonText : $t("APPLOGIN_LOGIN") }}
        </nn-button>
      </div>
      <cms-content :content-url="config.loginFormContent"></cms-content>
    </form>
    <loading-modal ref="loadingModal"></loading-modal>
  </div>
</template>

<script>
import CmsContent from "../shared/CmsContent.vue";
import loginService from "./loginService";
import errorState from "./errorState";
import LoadingModal from "../components/LoadingModal"
import { findAncestor } from '@nn/psdl-utility/src/dom-helpers.js';
import { getTabIndex } from '../shared/tabindex';
import cookieUtils from '../shared/cookieUtils';
import analytics from '../shared/analytics';

export default {
  name: 'LoginForm',
  components: { CmsContent, LoadingModal },
  props: {
    config: {
      type: Object,
      default: () => ({}),
    },
  },
  data: () => ({
    username: '',
    password: '',
    remember: false,
    error: null,
    loading: false,
    capsLockInfo: '',
  }),
  mounted() {
    if (this.config.focusUserNameOnLoad) {
      this.$refs.username.focus();
    }
  },
  computed: {
    invalidFormError() {
      return this.error === errorState.invalid;
    },
    serviceError() {
      return this.error === errorState.serviceError;
    },
    tabindex() {
      return getTabIndex();
    },
  },
  methods: {
    async login() {
      if (!this.isFormValid()) {
        return this.error = errorState.invalid;
      }
      this.openAndPositionModal();
      this.error = null;

      try {
        await loginService.login(this.username, this.password.trim());
        cookieUtils.setLoginTabCookie('form');
        analytics.publishLoginFormTracking('succes', 'login-form');
        if (this.remember) {
          loginService.rememberUsername(this.username.trim());
        } else {
          loginService.forgetUsername();
        }
      } catch (e) {
        await this.$refs.loadingModal.close();
        if(e === errorState.mfaEnabled) {
          analytics.publishMfaTracking('force-mfa', 'mfa-enabled');
          this.$emit('mfaEnabled');
        } else {
          this.error = e;
          analytics.publishLoginFormTracking('error', this.invalidFormError ? 'invalid form' : 'login service error')
        }
      }
    },
    id(suffix) {
      return `${this._uid}_${suffix}`;
    },
    isFormValid() {
      return this.username && this.username.length && this.password && this.password.trim().length
    },
    async openAndPositionModal() {
      await this.$refs.loadingModal.open();
      const loadingModal = document.querySelector('#loading-modal');
      if(findAncestor(loadingModal, '[data-role="dialog"]')) {
        document.body.appendChild(loadingModal);
      }
    },
    onKeydown(event){
      if(event && event.getModifierState && event.getModifierState('CapsLock')){
        document.msCapsLockWarningOff = true;
        this.capsLockInfo = this.$t('APPLOGIN_CAPS_LOCK');
      } else{
        this.capsLockInfo = '';
      }
    },
    onBlur(){
       this.capsLockInfo = '';
    },
  },
  beforeMount() {
    loginService.setConfig(this.config);
    this.username = loginService.getSavedUsername();
    this.remember = this.username.length > 0;
  }
}
</script>

<style lang="scss" scoped>
  form {
    overflow: hidden;
  }
</style>
