import template from './contractList.vue.html';
import ContractItem from './contractItem';
import { mapActions, mapState } from 'vuex';
import { store } from './gekoStore';

export default {
  template,
  components: {
    ContractItem: ContractItem
  },
  store,
  async mounted() {
    await this.getContracts();
    await this.getBalances();
  },
  computed: {
    ...mapState(['account', 'language', 'contractsState', 'contracts']),
  },
  methods: {
    ...mapActions([
      'getContracts',
      'getBalances',
    ]),
  }
}
