import { asArray, isEmpty } from "@nn/psdl-utility/src/object-utils";

const ICONS = {
  'Banksparen': 'personal-savings',
  'Verzekeren': 'umbrella',
  'Hypotheken': 'house',
  'Pensioen': 'boat',
  'Overig': 'folder',
  'Sparen': 'savings-account',
  'Beleggen': 'tree',
  'Zorg': 'zorg'
};

function getCategory(collection, catName) {
  return collection.filter((category) => {
    return category.categoryName === catName;
  })
    .reduce((prev, current) => {
      return current;
    }, null);
}

function parseContractData(contractData) {
  if (isEmpty(contractData)) {
    return [];
  }

  if (!Array.isArray(contractData)) {
    contractData = [contractData];
  }

  let contractListByCategory = [];
  contractData.forEach((contract, index) => {

    let category = getCategory(contractListByCategory, contract.Category);
    if (!category) {

      category = {
        categoryName: contract.Category,
        contracts: [],
        icon: {}
      }
      category.icon['c-icon--' + ICONS[contract.Category]] = true;
      contractListByCategory.push(category);
    }

    category.contracts.push(parseContractState(contract, index));
  });
  return contractListByCategory;
}

function parseContractState(contract, index) {

  if (isEmpty(contract)) {
    return null;
  }

  const contractDate = new Date(contract.EndDate);
  const today = new Date();
  let endDate = null;

  if (/\d{4}-\d{2}-\d{2}/.test(contract.EndDate) && contractDate < today) {
    endDate = contract.EndDate;
  }

  contract.StatusNew = (contract.Role === 'AA');
  contract.StatusEnd = endDate;
  contract.contractIndex = index;
  if (isContractForBalance(contract)) {
    contract.saldoLoadingState = 'loading'
  }

  if (contract.ContractSubParts && contract.ContractSubParts.ContractSubPart) {
    contract.subContracts = asArray(contract.ContractSubParts.ContractSubPart);
    delete contract.ContractSubParts;
  } else {
    contract.subContracts = [];
  }

  return contract;
}

function parseActionData(actionData) {

  if (isEmpty(actionData)) {
    return [];
  }

  if (!Array.isArray(actionData)) {
    actionData = [actionData];
  }

  return actionData;
}

function isContractForBalance(contract) {
  if (contract.StatusNew || contract.StatusEnd) {
    return false;
  }
  const productcodesForBalance = ['N300', 'N730', 'N733', 'N790', 'F530'];
  return (contract.Category === 'Beleggen' || productcodesForBalance.includes(contract.PRODUCTCODE))
}

export {
  parseContractData,
  parseActionData,
  parseContractState,
  isContractForBalance,
}
