import { mapActions, mapState } from 'vuex';
import Template from './notificationList.vue.html';
import NotificationItem from './notificationItem';
import * as location from '@nn/psdl-utility/src/location';
import { putGetAndRedirect } from '../utility/redirect';
import { createNotificationProfile } from '../utility/profilesFactory';
import analyticsMixin from '../app-vue-shared/analyticsMixin';

const NotificationList = {
  mixins: [analyticsMixin],
  template: Template,
  components: {
    NotificationItem: NotificationItem
  },
  computed: {
    ...mapState([
      'notifications',
      'config'
    ]),
  },
  methods: {
    ...mapActions([
      'getNotifications',
      'removeNotification'
    ]),
    onCallToAction: function (item, redirectUrl) {
      this.analytics.publish('element-tracking', 'notificationcentre', {
        'event_action': 'click',
        'event_label': item.Message.TrackCode,
      });

      if (item.Action.ParameterFormat) {
        const notificationProfile = createNotificationProfile({
          contractNumber: item.Contract.VTA_Contract_id,
          messageCode: item.Message.Code,
          vtaId: item.Contract.VTA_id,
        });

        putGetAndRedirect({
          redirectUrl,
          profile: notificationProfile,
          action: item.Action,
        });
      } else {
        location.assign(redirectUrl);
      }
    },
    onRemoveNotification: function (notification, context) {
      this.analytics.publish('element-tracking', 'notificationcentre', {
        'event_action': 'close',
        'event_label': notification.Message.TrackCode,
      });

      this.removeNotification({ notification, context });
    }
  },
  mounted: async function () {
    await this.getNotifications(this.feed);

    this.analytics.prop({
      nn_mb_id: this.notifications.map(n => n.Message.TrackCode).join(','),
      nn_mb_counter: this.notifications.length
    });
  }
};

export default NotificationList;
