import scrapeContent from "../../utility/content-scraper";

const content = {};

export function getContentFromUrl(url) {
  if (!url) {
    return Promise.resolve('');
  }

  if (content[url]) {
    return Promise.resolve(content[url]);
  }

  return scrapeContent(url, true).then(cmsContent => {
    content[url] = cmsContent;
    return cmsContent;
  })
}