import http from "../../app-utilities/http";

let url, headers;

export function setConfig(config) {
  url = config.urlQrService;
  headers = config.headers;
}

export function getOutput(response) {
  let output = {};
  response.callbacks.forEach(c => {
    output = c.output.reduce((result, o) => {
      result[o.name] = o.value;
      return result;
    }, {});
  });
  return output
}

export function getQrCode() {
  if(typeof headers === 'string') {
    headers = headers.split(',').reduce((result, pair) => {
      const parts = pair.split('=');
      result[parts[0]] = parts[1];
      return result;
    }, {});
  }

  return http.post(url, {}, {
    headers: {
      ...headers,
      'Content-Type': 'application/json',
      'x-requested-with': 'XMLHttpRequest',
    }
  }).then(response => {
    const output = getOutput(response);
    return {code: output.message, body: response};
  })
}

export function checkLoginResult(body) {
  return http.post(url, body, {
    headers: {
      ...headers,
      'Content-Type': 'application/json',
      'x-requested-with': 'XMLHttpRequest',
    }
  });
}

export default {
  getQrCode,
  checkLoginResult,
  setConfig
}