<template>
  <div>
    <nn-notification type="error" v-if="getQrError || loginQrError" class="u-maring-bottom">
      <div v-if="getQrError">{{$t("APPLOGIN_QR_GETERROR")}}</div>
      <div v-if="loginQrError">{{$t("APPLOGIN_QR_LOGINERROR")}}</div>
      <nn-button @click="getQrCode">{{$t("APPLOGIN_QR_BUTTON")}}</nn-button>
    </nn-notification>
    <div v-else class="u-flex u-flex--wrap u-margin-bottom">
      <nn-spinner v-if="!qrCode" class="qr-width-height u-margin-right--30">{{$t("APPLOGIN_QR_LOADING")}}</nn-spinner>
      <qr-code v-else :qrCode="qrCode" :timeout="timeout" @refresh="getQrCode" @error="publishQrCodeError" class="u-margin-right--30"></qr-code>
      <ol class="u-margin-top">
        <li class="u-margin-bottom" v-for="step in [0,1,2]">
          {{$t(`APPLOGIN_QR_STEPS[${step}]`)}}
        </li>
      </ol>
    </div>
    <loading-modal ref="loadingModal"></loading-modal>
    <cms-content :content-url="config.loginQrContent"></cms-content>
  </div>
</template>

<script>
import QrCode from "./QrCode.vue";
import LoadingModal from "../components/LoadingModal";
import CmsContent from "../shared/CmsContent.vue";
import qrService from "./qrService";
import { redirect } from "../shared/redirect";
import cookieUtils from '../shared/cookieUtils';
import analytics from '../shared/analytics';

export default {
  name: "LoginWithQr",
  components: { CmsContent, QrCode, LoadingModal },
  props: {
    config: {
      type: Object,
      default: () => ({})
    }
  },
  data: () => ({
    qrCode: '',
    getQrError: false,
    loginQrError: false,
    success: false,
    pollInterval: null,
  }),
  methods: {
    getQrCode() {
      this.loginQrError = false;
      this.getQrError = false;
      qrService.getQrCode().then(response => {
        this.qrCode = response.code;
        this.poll(response.body);
      }).catch(() => {
        analytics.publishMfaTracking('error', 'get-qr-code');
        this.getQrError = true
      });
    },
    poll(body) {
      let requestBody = body;
      this.pollInterval = setInterval(() => {
        qrService.checkLoginResult(requestBody).then(result => {
          if (result.callbacks) {
            requestBody = result;
            return;
          }
          if (result.successUrl) {
            analytics.publishMfaTracking('succes', 'login-qr-code');
            this.$refs.loadingModal.open();
            cookieUtils.setLoginTabCookie('qrCode');
            clearInterval(this.pollInterval);
            this.success = true;
            redirect(this.config.orgUrl, this.config.loginRedirect);
          }
        }).catch(() => {
          clearInterval(this.pollInterval);
          analytics.publishMfaTracking('error', 'login-qr-code');
          this.loginQrError = true;
        });
      }, this.interval);

      setTimeout(() => {
        clearInterval(this.pollInterval);
      }, this.timeout);
    },
    publishQrCodeError(event){
      this.getQrError = true;
      clearInterval(this.pollInterval);
      analytics.publishMfaTracking('error', event);
    }
  },
  mounted() {
    qrService.setConfig(this.config);
    this.getQrCode();
  },
  computed: {
    interval() {
      return this.config.intervalSeconds ? parseInt(this.config.intervalSeconds) * 1000 : 2000;
    },
    timeout() {
      return this.config.timeoutSeconds ? parseInt(this.config.timeoutSeconds) * 1000 : 30000;
    },
  },
  destroyed() {
    if (this.pollInterval) {
      clearInterval(this.pollInterval);
    }
  },
}
</script>

<style scoped>
  .qr-width-height {
    width: 180px;
    height: 180px;
  }
</style>