import Vue from 'vue';
import VueComponents from '@nn/vue-components'

import NotificationList from './notificationList.js'
import {store} from './store.js';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';

Vue.use(VueComponents);

export default class NotificationCenter {
  constructor (root, config) {
    this.root = root;
    this.config = config;
  }

  init () {
    store.dispatch('setConfig', this.config);

    this.app = new Vue({
      el: createVueChildContainer(this.root),
      store,
      render: h => h(NotificationList)
    });

    return this.app;
  }
}
