import Vue from 'vue';
import VueComponents from '@nn/vue-components';
import VueI18n from 'vue-i18n';
import LoginForm from './LoginForm.vue';
import { queryString } from '@nn/psdl-utility/src/query-string.js';
import { translations } from '../i18n.js';
import { getCurrentLanguage, getFallbackLanguage } from '../../utility/i18n';
import { createVueChildContainer } from '../../app-vue-shared/createVueChildContainer';
import configDefaults from "../configDefaults";

Vue.use(VueComponents);

export default function bootstrapLoginForm(root, config) {

  Vue.use(VueI18n);
  const i18n = new VueI18n({
    locale: getCurrentLanguage(),
    fallbackLocale: getFallbackLanguage(),
    messages: translations,
  });

  return new Vue({
    el: createVueChildContainer(root),
    i18n,
    render: h => h(LoginForm, {
      props: {
        config: {
          ...configDefaults,
          ...config,
          orgUrl: queryString('orgurl') || queryString('orgUrl')
        }
      }
    }),
  });
}
