import Vue from 'vue';
import VueComponents from '@nn/vue-components';
import ContractList from './contractList.js';
import { createVueChildContainer } from '../app-vue-shared/createVueChildContainer';

import './_c-contract-list.scss';
import '../app-vue-shared/shared.js';

Vue.use(VueComponents);

class GekoProductOverview {

  /**
   * @param {HTMLNode} root
   * @returns {GekoProductOverview}
   */
  constructor(root) {
    this.root = root;
    this.app = new Vue(ContractList);
  }

  init() {
    return this.app.$mount(createVueChildContainer(this.root));
  }
}


export default GekoProductOverview;
