import { jsonToXml } from "../app-utilities/utilities";

export function createContractProfile(data, origin) {
  const profileData = new ContractProfileData(data);
  return new Profile('ContractProfile', profileData, origin || 'GEKO');
}

export function createNotificationProfile(data) {
  const profileData = new NotficationProfileData(data);
  return new Profile('NotificationProfile', profileData, 'NOTIFICATIONCENTER');
}

export function createCorporateProfile(data) {
  const profileData = new CorporateProfileData(data);
  return new Profile('CorporateProfile', profileData, 'GEZO');
}

export class NotficationProfileData {
  constructor (data) {
    if (!data) {
      throw new Error('Cannot create a notification profile data without data');
    }
    this.contractNumber = data.contractNumber;
    this.messageCode = data.messageCode;
    this.vtaId = data.vtaId;
  }
}

class ContractProfileData {
  constructor (data) {
    if (!data) {
      throw new Error('Cannot create a contract profile data without data');
    }
    this.enddate = data.enddate;
    this.externalId = data.externalId;
    this.externalIdAdm = data.externalIdAdm;
    this.contractNumber = data.contractNumber;
    this.language = data.language;
    this.mrdbid = data.mrdbid;
    this.productCode = data.productCode;
    this.roleid = data.roleid;
    this.rolenumber = data.rolenumber;
    this.sapbp_srid = data.sapbp_srid;
    this.sapbpId = data.sapbpId;
    this.vaNumber = data.vaNumber;
    this.vtaId = data.vtaId;
  }
}

export class CorporateProfileData {
  constructor (data) {
    if (!data) {
      throw new Error('Cannot create a corporate profile data without data');
    }
    this.employerNumber = data.employerNumber;
    this.dataId = data.dataId;
    this.sapbp_srId = data.sapbp_srId;
    this.sapbpId = data.sapbpId;
    this.userOrganisationId = data.userOrganisationId;
  }
}

class Profile {
  constructor(name, data, origin) {
    this.name = name;
    this.ticketXml = jsonToXml({[name]: data}, true)
    this.origin = origin;
  }
}
